import React from "react";
import Typist from "react-typist";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";
import "react-typist/dist/Typist.css";

const MainBody = React.forwardRef(
  ({ gradient, title, message, icons }, ref) => {
    return (
      <Jumbotron
        fluid
        id="home"
        style={{
          background: `linear-gradient(136deg,${gradient})`,
          backgroundSize: "1200% 1200%",
        }}
        className="title bg-transparent bgstyle text-light min-vh-100 d-flex align-content-center align-items-center flex-wrap m-0"
      >
        <div id="stars"></div>
        <Container className="text-center">
          <h1 ref={ref} className="display-1">
            {title}
          </h1>
          <Typist
            className="lead typist"
            cursor={{
              show: true,
              blink: true,
              element: "|",
              hideWhenDone: false,
            }}
          >
            <Typist.Delay ms={700} />
            <h1 style={{ display: "inline", fontSize: 22 }}>
              <b style={{ color: "#ffb617", fontSize: 22}}>!</b>false
            </h1>
            <Typist.Delay ms={700} />
            <h1 style={{ display: "inline", fontSize: 20 }}>&nbsp;&nbsp;it's funny because it's <b style={{ color: "#ffb617", fontSize: 22}}>true</b></h1>
          </Typist>
          <div className="p-5">
            {icons.map((icon, index) => (
              <a
                key={`social-icon-${index}`}
                target="_blank"
                rel="noopener noreferrer"
                href={icon.url}
                aria-label={`My ${icon.image.split("-")[1]}`}
              >
                <i className={`fab ${icon.image}  fa-3x socialicons`} />
              </a>
            ))}
          </div>
          <a
            className="btn btn-outline-light btn-lg "
            href="#aboutme"
            role="button"
            aria-label="Learn more about me"
          >
            Mais sobre mim
          </a>
        </Container>
      </Jumbotron>
    );
  }
);

export default MainBody;
