import React from "react";
import { Col } from "react-bootstrap";
import { technologies } from "../../editable-stuff/config.js";

const ExperienceProjectCard = ({ project }) => {
  return (
    <Col lg="6">
      <div className="pb-5 text-center">
        <p className="lead">
          <p className="lead">
            {project.technologies.map((technologyName, index) => {
              let isMarginNeeded = false;
              return technologies.map((t) => {
                if (index > 0) isMarginNeeded = true;
                if (t.name === technologyName) {
                  return (
                    <img
                      className=" bg-white mb-3"
                      src={t.icon}
                      alt=""
                      style={isMarginNeeded ? { marginLeft: 15 } : {}}
                    />
                  );
                }
              });
            })}
            <br />
            {project.technologies.map((technologyName, index) => {
              if (
                (index === project.technologies.length) === 1 ||
                index === project.technologies.length - 1
              )
                return technologyName;
              else return technologyName + ", ";
            })}
            <br />
            {project.context}
          </p>
        </p>
      </div>
    </Col>
  );
};

export default ExperienceProjectCard;
