import React from "react";
import ExperienceCompanyCard from "./ExperienceCompanyCard";
import ExperienceProjectCard from "./ExperienceProjectCard";
import { Container, Row, Jumbotron } from "react-bootstrap";

const Experience = ({ experiences }) => {
  return (
    <section className="section">
      <Container>
        <Jumbotron fluid className="bg-white" id="experience">
          <h2 className="display-4 mb-5 text-center p-5">{experiences.heading}</h2>
          <Row>
            {experiences.companies.map((company) => {
              return <ExperienceCompanyCard key={company} company={company} />;
            })}
          </Row>
          <h2 className="display-4 mb-5 text-center">Projetos</h2>
          <Row style={{ marginTop: 50 }}>
            {experiences.projects.map((project) => {
              return <ExperienceProjectCard key={project} project={project} />;
            })}
          </Row>
        </Jumbotron>
      </Container>
    </section>
  );
};

export default Experience;
